.start {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 100%;
}
@media (max-width:1280px) {
  .start {
    width: 70%;
  }
}
@media (max-width:960px) {
  .start {
    width: 80%;
    height: 80%;
  }
}
@media (max-width:430px) {
  .start {
    width: 100%;
    height: 60%;
  }
}
.start .upSide, .start .downSide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  transform: translate(-50% -50%);
  width: 90%;
  height: 48%;
  background-color: black;
  color: white;
}
.start .upSide {
  margin-bottom: 2%;
}
.start .upSide .imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 85%;
}
.start .upSide .imageContainer .title {
  max-width: 100%;
  max-height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.start .upSide .startText {
  width: 70%;
  height: 13%;
  text-align: center;
  font-size: 24px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  letter-spacing: 3px;
}
@media (max-width:960px) {
  .start .upSide .startText {
    font-size: 16px;
  }
}
@media (max-width:430px) {
  .start .upSide .startText {
    font-size: 12px;
  }
}
.start .downSide .centerParts {
  width: 100%;
  height: 100%;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1.2fr 3.6fr 1.2fr;
}
.start .downSide .centerParts .crossKey {
  margin: auto 0;
  font-size: 150px;
}
@media (max-width:960px) {
  .start .downSide .centerParts .crossKey {
    font-size: 100px;
  }
}
@media (max-width:430px) {
  .start .downSide .centerParts .crossKey {
    font-size: 60px;
  }
}
.start .downSide .centerParts .gameFrame {
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.start .downSide .centerParts .gameFrame .sampleVideo {
  max-width: 100%;
  max-width: 100%;
}
.start .downSide .centerParts .fourKeys {
  height: 100%;
  position: relative;
}
.start .downSide .centerParts .fourKeys .commandKey {
  cursor: pointer;
  border-radius: 20px;
  font-size: 35px;
  position: absolute;
  margin: 0 auto;
  top: 50%;
  left: 40%;
  animation: buttonAnimate linear 1s infinite;
  transform: translate(-50% -50%);
}
@media (max-width:960px) {
  .start .downSide .centerParts .fourKeys .commandKey {
    font-size: 25px;
  }
}
@media (max-width:430px) {
  .start .downSide .centerParts .fourKeys .commandKey {
    font-size: 16px;
  }
}
.start .downSide .centerParts .fourKeys .commandKey:nth-child(1) {
  background-color: skyblue;
  transform: translateY(-100%);
}
.start .downSide .centerParts .fourKeys .commandKey:nth-child(2) {
  color: black;
  background-color: firebrick;
  transform: translateY(-50%);
  transform: translateX(200%);
  animation-delay: 0.3s;
}
.start .downSide .centerParts .fourKeys .commandKey:nth-child(3) {
  background-color: skyblue;
  transform: translateY(100%);
  animation-delay: 0.6s;
}
.start .downSide .centerParts .fourKeys .commandKey:nth-child(4) {
  color: black;
  background-color: firebrick;
  transform: translateY(-50%);
  transform: translateX(-200%);
  animation-delay: 0.9s;
}
.start .downSide p {
  text-align: center;
}

@keyframes buttonAnimate {
  from {
    opacity: 0.7;
  }
  to {
    opacity: 1;
  }
}/*# sourceMappingURL=Start.css.map */