@use "../../assets/responsive" as *;

.start{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 60%;
    height: 100%;
    @include mq(lg){
      width: 70%;
    }
    @include mq (md){
      width: 80%;
      height: 80%;
    }
    @include mq(sm){
      width: 100%;
      height: 60%;
      
    }
  
    & .upSide,& .downSide{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 30px;
        margin: 0 auto;
        top: 50%;
        left:50%;
        transform: translate(-50% -50%);
        width: 90%;
        height: 48%;
        
        background-color: black;
        color: white;
    }
    & .upSide{
        margin-bottom: 2%;
      & .imageContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70%;
        height: 85%;
        & .title{
          max-width: 100%;
          max-height: 100%;
          object-fit: cover;
        }           
      }
      & .startText{
        width: 70%;
        height: 13%;
        text-align: center;
        font-size: 24px;
        @include mq(md){
          font-size: 16px;
        }
        @include mq(sm){
          font-size: 12px;
        }
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        letter-spacing: 3px;
      }
    }

    & .downSide{
      & .centerParts{
        width: 100%;
        height: 100%;
        display: grid;
        justify-content: center;
        align-items: center;
        grid-template-columns: 1.2fr 3.6fr 1.2fr;
        & .crossKey{
          margin: auto 0;
          font-size: 150px;
          @include mq(md){
            font-size: 100px;
          }
          @include mq(sm){
            font-size: 60px;
          }
          
        }
        & .gameFrame{
          height: 70%;
          display: flex;
          justify-content: center;
          align-items: center;
          & .sampleVideo{
            max-width: 100%;
            max-width: 100%;
          }
        }
        & .fourKeys{
          height: 100%;
          position: relative;
          
          & .commandKey{
            cursor: pointer;
            border-radius: 20px;
            font-size: 35px;
            position: absolute;
            margin: 0 auto;
            top: 50%;
            left: 40%;
            animation: buttonAnimate linear 1s infinite;
            transform: translate(-50% -50%);
            @include mq(md){
              font-size: 25px;
            }
            @include mq(sm){
              font-size: 16px;
            }
            &:nth-child(1){
              background-color: skyblue;
              transform: translateY(-100%);
            }
            &:nth-child(2){
              color: black;
              background-color: firebrick;
              transform: translateY(-50%);
              transform: translateX(200%);
              animation-delay: 0.3s;
            }
            &:nth-child(3){
              background-color: skyblue;
              transform: translateY(100%);
              animation-delay: 0.6s;
            }
            &:nth-child(4){
              color: black;
              background-color: firebrick;
              transform: translateY(-50%);
              transform: translateX(-200%);
              animation-delay: 0.9s;
            }
          }   
        }
      }
      & p{
        text-align: center;
      }
    }
  }

  @keyframes buttonAnimate {
    from{
      opacity: 0.7;
    }
    to{
      opacity: 1;
    }
  }